import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { InAppBrowser } from '@ionic-native/in-app-browser';

@IonicPage()

@Component({
  selector: 'page-aerodrome-data',
  templateUrl: 'aerodrome-data.html',
})

export class AerodromeDataPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, private iab: InAppBrowser) {

    const browser = this.iab.create('http://localhost:5155/docs/MIAL_AO_DOC_01_00 Aerodrome Manual Jan 2019-1.pdf');
    browser.show();
    this.navCtrl.setRoot('HomePage');

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AerodromeDataPage');
  }

}



